/**
 *  An actual issue.
 *  Conflicts is true if the issue conflicts to an answer of
 *  at least on question.
 *  answers is a list of true/false for each question.
 */
class Issue {
    constructor(id, text, answers) {
        this.id = id;
        this.text = text;
        this.conflicts = false;
        this.answers = answers;
    }

    getId = () => {
        return this.id;
    }

    setConflicts = () => {
        this.conflicts = true;
    }

    getConflicts = () => {
        return this.conflicts;
    }

    getAnswer = (questionId) => {
        return this.answers[questionId] === 1 ? true : false;
    }

    getText = () => {
        return this.text;
    }
}

/** 
 *  A question.
 *  id: an id
 *  text: the actual question
 *  shortdesc: an short description
 */
class Question {
    constructor(id, text, shortdesc) {
        this.id = id;
        this.text = text;
        this.asked = false;
        this.shortdesc = shortdesc;
    }

    getId = () => {
        return this.id;
    }

    setAsked = () => {
        this.asked = true;
    }

    getAsked = () => {
        return this.asked;
    }

    getText = () => {
        return this.text;
    }

    getDescription = () => {
        return this.shortdesc;
    }
}

/** 
 *  A solution to the issue with issue_id.
 *  id: an id
 *  text: the actual question
 *  shortdesc: an short description
 */
class Solution {
    constructor(id, issue_id, shortdesc, text) {
        this.id = id;
        this.issue_id = issue_id;
        this.shortdesc = shortdesc;
        this.text = text;
    }

    getId = () => {
        return this.id;
    }

    getIssueId = () => {
        return this.issue_id;
    }

    getDescription = () => {
        return this.shortdesc;
    }

    getText = () => {
        return this.text;
    }
}

/**
 *  The fault report AI, aka För Valter.
 *  Handles which question should be asked
 *  and find the issue.
 *
 *  Questions and issues should be fetched from backend?
 */
class Report {
    constructor() {
        this.questions = [
            new Question(0, "Är problemet inomhus?", "Inomhus"),
            new Question(1, "Är problemet i lägenheten?", "I lägenheten"),
            new Question(2, "Är problemet i badrummet?", "I badrummet"),
            new Question(3, "Är problemet i köket?", "I köket"),
            new Question(4, "Är problemet i tvättstugan?", "I tvättstugan"),
            new Question(5, "Har du problem med en vitvara?", "En vitvara"),
            new Question(6, "Har du problem med el?", "Elen"),
            new Question(7, "Har du problem med vatten?", "Vattnen"),
        ];

        this.issues = [
            new Issue(0, "Badkar",              [1, 1, 1, 0, 0, 0, 0, 0]),
            new Issue(1, "Spisen",              [1, 1, 0, 1, 0, 1, 0, 0]),
            new Issue(2, "Tvättmaskin",         [1, 1, 1, 0, 0, 1, 0, 0]),
            new Issue(3, "Nerskräpning",        [0, 0, 0, 0, 0, 0, 0, 0]),
            new Issue(4, "Delad tvättmaskin",   [1, 0, 0, 0, 1, 1, 0, 0]),
            new Issue(5, "Delad torktumlare",   [1, 0, 0, 0, 1, 1, 0, 0]),
            new Issue(6, "Torktumlare",         [1, 1, 1, 0, 0, 1, 0, 0]),
            new Issue(7, "Spisfläkt",           [1, 1, 0, 1, 0, 1, 0, 0]),
        ];

        // List of solutions connected to issues?
        this.solutions = [
            new Solution(0, 7, "Problem med fläktfiltret?", 
                "Som hyresgäst ansvarar du själv för filtret i fläkten."),
            new Solution(1, 7, "Köpa nytt fläktfiltret?", 
                "Som hyresgäst ansvarar du själv för att köpa nytt filtret till\
                fläkten. Detta kan du göra billigast på FÖRETAG X."),
        ];

        this.moreQuestions = true;
        this.issueSuggestion = false;
        this.foundIssue = null;
        this.foundSolution = false;
        this.currentQuestion = this.questions[0];
        this.questions[0].setAsked();
    }
        
    /**
     *  Get the text for current question.
     */
    getQuestionText = () => {
        return this.currentQuestion.getText();
    }

    /**
     *  Get the description for current question.
     */
    getQuestionDescription = () => {
        return this.currentQuestion.getDescription();
    }

    /**
     *  Returns if there are more questions that
     *  can be asked.
     */
    hasMoreQuestions = () => {
        return this.moreQuestions;
    }

    hasIssueSuggestion = () => {
        return this.issueSuggestion;
    }

    /**
     *  Find the next question to ask.
     *  Base on:
     *  - The question is not already asked.
     *  - There are issue that depend on the question.
     */
    nextQuestion = () => {
        // look for a unasked question
        let best_score = 0;
        let best_question_id = -1;
        let questionFound = false;
        const eps = 0.00000000001;
        for(let q in this.questions) {
            let question = this.questions[q];
            if(!question.getAsked()) {

                // Make sure the question is the most relevant one
                let counter = 0;
                for(let i in this.issues) {
                    let issue = this.issues[i];
                    if(!issue.getConflicts() && issue.getAnswer(q)) {
                        counter++;
                    }
                }
                const posprob = counter/this.issues.length;
                const score = 
                    -(posprob)*Math.log(posprob+eps) 
                    +(1-posprob)*Math.log(1-posprob+eps);
                if (score > best_score && counter !== 0) {
                    best_score = score;
                    best_question_id = q;
                    questionFound = true;
                }
            }
        }

        if(questionFound) {
            this.currentQuestion = this.questions[best_question_id];
            this.questions[best_question_id].setAsked();
        } else {
            this.moreQuestions = false;
            for (let i in this.issues) {
                if(!this.issues[i].getConflicts()) {
                    this.issueSuggestion = true;
                }
            }
        }
    }

    /** 
     *  Returns an object of all issues left.
     *  IMPROVEMENT: Only return the 5 most likely.
     */
    getIssueSuggestion = () => {
        let suggestions = {};
        for (let i in this.issues) {
            let issue = this.issues[i];
            if(!issue.getConflicts()) {
                suggestions[i] = issue.getText();
            }
        }
        return suggestions;
    }

    /**
     *  Get the answer from the suggested issues.
     */
    setSuggestionAnswer = (id) => {
        if(id !== -1) {
            this.foundIssue = this.issues[id];
            // Check for solution
            for (let s in this.solutions) {
                if(this.solutions[s].getIssueId() === this.foundIssue.getId()) {
                    console.log("Found solution! " + s);
                    this.foundSolution = true;
                    break;
                }
            }
        }
        this.issueSuggestion = false;   
    }

    /**
     *  Mark issues that conflicts to the answer
     *  of the current question.
     */
    setQuestionAnswer = (answer) => {
        let new_issues = [];
        for(let i in this.issues) {
            let issue = this.issues[i];
            if(issue.getAnswer(this.currentQuestion.getId()) !== answer) {
                issue.setConflicts();
            } else {
                new_issues.push(issue);
            }
        }

        this.issues = new_issues;
    }

    /**
     *  Returns the chosen issue.
     */
    getResult = () => {
        if(this.foundIssue !== null) {
            return this.foundIssue.getText();
        }
        return null;
    }

    /**
     *  Return true if there exists a possible solution 
     *  to the selected issue.
     */
    hasSolution = () => {
        return this.foundSolution;
    }

    /**
     *  returns a solution for this.foundissue
     */
    getSolutions = () => {
        // Returns a solution for this.foundIssue
        let sols = {}
        for (let s in this.solutions) {
            if(this.solutions[s].getIssueId() === this.foundIssue.getId()) {
                sols[s] = [
                    this.solutions[s].getDescription(), 
                    this.solutions[s].getText(), 
                ];
                this.foundSolution = true;
            }
        }
        console.log("solutions: " + sols);
        return sols;
    }

    /**
     *  Set the answer for the suggested solution.
     *  answer: boolean
     */
    setSolutionAnswer = (answer) => {
        if(answer) {
            // We are done!
        } else {
            // Let user report issue.
        }
        this.foundSolution = false;
    }
}

export default Report;
