import React, { Component } from 'react';
import Report from './Report';
import './App.scss';
import Logotype from './img/logo.png';
import * as emailjs from 'emailjs-com';

class FaultReport extends Component {
    constructor(props) {
        super(props);
        this.state = {
            report: new Report(),
            qcount: 0, // Just to update the component
            textarea_value: '',
            textname_value: '',
            textemail_value: '',
            positive_answers: [],
        };

    }

    resetReport =() => {
        this.setState({report: new Report()});
        this.setState({qcount: 0});
        this.setState({textarea_value: ''});
        this.setState({textname_value: ''});
        this.setState({textemail_value: ''});
        this.setState({positive_answers: []});
    }

    handleClick = (idx) => {
        this.setState({currentChoice: idx});
    }

    getTextbox = () => {
        return (
            <div className="FaultReportText">
            <div className="FaultReportHeader">Beskriv problemet:</div>
            <textarea rows="20" cols="50" />
            <br/>
            <button className="FaultReportButton">Skicka</button>
            </div>
        );
    }

    getNextQuestion = () => {
        let q = this.state['report'].getQuestion();
        if(q != null) {
            this.setState({currentQuestion: q});
        } else {
            this.setState({moreQuestions: false});
        }
    }

    answerReport = (answer) => {
        if(answer) {
            this.state['positive_answers'].push(
                this.state['report'].getQuestionDescription());
        }
        console.log(this.state['positive_answers']);
        this.state['report'].setQuestionAnswer(answer);
        this.state['report'].nextQuestion();
        this.setState({'qcount': this.state['qcount'] + 1}); // update component
    }

    answerSuggestion = (id) => {
        this.state['report'].setSuggestionAnswer(id); 
        this.setState({'qcount': this.state['qcount'] + 1});
    }

    answerSolution = (id) => {
        if (id == -1) {
            this.state['report'].setSolutionAnswer(id); 
            this.setState({'qcount': this.state['qcount'] + 1});
        } else {
            this.resetReport();
        }
    }

    getResult = () => {
        if(this.state['report'].getResult() != null) {
            return (<span>Valt problem: {this.state['report'].getResult()}</span>);
        } else {
            return (<span>Vi hittade inte ditt problem.</span>);
        }
    }

    handleChange = (e) => {
        this.setState({[e.target.name]: e.target.value});
    }

    sendReport = (e) => {
        e.preventDefault();
        let templateParams = {
            from_name: "Nejbor",
            to_name: this.state['textname_value'],
            report_id: Math.floor(Math.random() * 1000) + 100,
            message_html: this.state['textarea_value'],
            to_email: this.state['textemail_value'],
            problem: '',
            answers: '',
        };

        if (this.state['report'].getResult() !== null) {
            templateParams['problem'] = this.state['report'].getResult();
        }

        templateParams['answers'] += "<ul>";
        for (let i in this.state['positive_answers']) {
            templateParams['answers'] += 
                "<li>" + this.state['positive_answers'][i] + "</li>";
        }
        templateParams['answers'] += "</ul>";

        emailjs.send(
            'gmail', 
            'template_7tYScFrN', 
            templateParams, 
            'user_z3koOKn5N8lxWwWIlvVob')
            .then(function (response) {
                console.log("Success!", response.status, response.text);
            }, function(err) {
                console.log(err);
            }
        );

    }

    getQuestions = () => {
        if(this.state['report'].hasMoreQuestions()) {
            return (
                <div className="FaultReportQuestions">
                <div className="FaultReportQuestionsQuestion">
                {this.state['report'].getQuestionText()}
                </div>
                <div className="FaultReportQuestionsChoices">
                <div className="FaultReportQuestionsItem"
                onClick={()=>this.answerReport(true)}>
                <div className="FaultReportQuestionsItemText">
                Ja
                </div>
                </div>
                <div className="FaultReportQuestionsItem NegChoice"
                onClick={()=>this.answerReport(false)}>
                <div className="FaultReportQuestionsItemText">
                Nej
                </div>
                </div>
                </div>
                </div>
            );
        } else if (this.state['report'].hasIssueSuggestion()) {
            const issues = this.state['report'].getIssueSuggestion();
            const res = Object.keys(issues).map( 
                function(key, index) {
                return (<div className="FaultReportSuggestionItem"
                    key={key}
                    onClick={()=>this.answerSuggestion(key)}>
                    {issues[key]}
                    </div>);
            }, this);
            return(
                <div className="FaultReportSuggestion">
                    Har du problem med något av följande?
                    <div className="FaultReportSuggestionChoices">
                        {res}
                        <div className="FaultReportSuggestionItem NegChoice"
                            onClick={()=>this.answerSuggestion(-1)}>
                            Nej
                        </div>
                    </div>
                </div>
            );
        } else if(this.state['report'].hasSolution()) {
            const solutions = this.state['report'].getSolutions();
            console.log("sols: " + solutions);
            const res = Object.keys(solutions).map( 
                function(key, index) {
                console.log(key + " : " + index);
                return (<div className="FaultReportSolutionItem"
                    key={key}
                    onClick={()=>{}}
                    tabIndex="1">
                    <div className="FaultReportSolutionItemHeader">
                        {solutions[key][0]}
                    </div>
                    <div className="FaultReportSolutionItemText">
                        {solutions[key][1]}
                    </div>
                    </div>);
            }, this);
            return(
                <div className="FaultReportSolutions">
                    Hej Valter här! 
                    <br/>
                    Här är några vanliga lösningar vid ditt
                    problem:
                    <div className="FaultReportSolutionChoices">
                        {res}
                        <br/>
                        Hjälpte detta med ditt problem?
                        <div className="FaultReportSolutionItem PosChoice"
                            onClick={()=>this.answerSolution(1)}>
                            Ja
                        </div>
                        <div className="FaultReportSolutionItem NegChoice"
                            onClick={()=>this.answerSolution(-1)}>
                            Nej
                        </div>
                    </div>
                </div>
            );
        } else {
            return (
                <div className="FaultReportResult">
                <div className="FaultReportResultQuestion">
                {this.getResult()}
                </div>
                <div className="FaultReportResultInput">
                <br/>
                Namn:
                <br/>
                <input 
                    type="text"
                    name="textname_value"
                    onChange={(e)=>this.handleChange(e)}/>
                <br/>
                Email:
                <br/>
                <input 
                    type="email"
                    name="textemail_value"
                    onChange={(e)=>this.handleChange(e)}/>
                <br/>
                Lägg till bild: 
                <br/>
                <input type="file" accept="image/*" capture/>
                <br/>
                <br/>
                Beskriv problemet:
                <br/>
                <br/>
                <textarea 
                    rows="10" cols="40"
                    name="textarea_value"
                    onChange={(e)=>this.handleChange(e)}/>
                <br/>
                <br/>
                <button type="button"
                onClick={(e)=>this.sendReport(e)}>
                Skicka Felanmälan</button
                >
                </div>
                </div>
            );
        }
    }

    render() {
        return (
            <div className="FaultReport">
            <img src={Logotype} alt=""/>
            <h1 className="FaultReportTitle">Felanmälan</h1>
            {this.getQuestions()}
            </div>
        );
    }
}

class App extends Component {
    render() {
        return (
            <div className="App">
            <div className="Screen">
            <FaultReport />
            </div>
            </div>
        );
    }
}

export default App;
